.SideDrawer {
	position: fixed;
	left: 0;
	top: 0;
	padding: 32px 16px;
	width: 280px;
	max-width: 70%;
	height: 100%;
	z-index: 200;
	background-color: #fff;
	box-sizing: border-box;
	transition: transform 0.3s ease-out;
}

@media (min-width: 500px) {
	.SideDrawer {
		display: none;
	}
}

.Open {
	transform: translateX(0);
}

.Close {
	transform: translateX(-100%);
}

.Logo {
	position: relative;
	left: -8px;
	display: inline-block;
	/* height: 11%; */
	height: 50px;
	margin-bottom: 32px;
}
